.animation_skip__button {
    position: absolute;
    height: 10vh;
    width: 10vh;
    top: 0;
    left: 0;
    margin: 10px;
    transition: .2s;
    background-color: #fff;
    color: #3dbacc;
    border-radius: 50%;
    font-size: 38px;
    border: 5px solid white;
    outline: none;

    &:hover {
        border: 5px solid white;
        background-color: #E7655C;
        color: white; } }
