.pizza_input {
    height: 24%;
    display: flex;
    flex-direction: column;
    align-items: stretch;

    @media(orientation: portrait) {
        visibility: hidden; }

    &--unos {
        margin-top: 12%; }

    &--dos {
        margin-bottom: 8%; }

    &__title {
        color: rgb(61, 186, 204);
        font-size: 5vh;
        margin: 5px 0; }

    &__form {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 10px 9; }

    &__label {
        display: block;
        font-size: 3vh; }

    &__input {
        text-align: center;
        flex-grow: 1;
        width: 20%;
        margin: 5px 0;
        padding: 4px;
        transition: .2s;
        font-size: 2vh;
        color: #292929;
        border: 2px solid rgb(227, 224, 224);
        border-radius: 10px;
        outline: none;

        &:focus {
            border: 2px solid rgb(61, 186, 204); } } }
